  public addStatus(data) {
    return new Promise((resolve, reject) => {
      this.httpService.prepareQuery('api/add-status', data)
        .then(() => {
            resolve();
          },
          (error) => {
            console.log('   ', error);
            reject();
          }
        );
    });
  }

